*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
}

.login-process {
    background-color: #c9d6ff;
    background: linear-gradient(to right, #e2e2e2, #c9d6ff);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 100vh;
}

.login-process .container {
    background-color: #fff;
    border-radius: 30px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.35);
    position: relative;
    overflow: hidden;
    width: 868px;
    max-width: 100%;
    min-height: 480px;
}

.login-process .container p {
    font-size: 20px;
    line-height: 20px;
    letter-spacing: 0.3px;
    margin: 20px 0;
}

.login-process .container span {
    font-size: 18px;
}

.login-process .container a {
    color: #333;
    font-size: 17px;
    text-decoration: none;
    margin: 15px 0 10px;
}

.login-process .container button {
    background-color: #0056b3;
    color: #fff;
    font-size: 16px;
    padding: 10px 20px;
    border: 1px solid transparent;
    border-radius: 8px;
    font-weight: 600;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    margin-top: 10px;
    cursor: pointer;
}

.login-process .container button.hidden {
    background-color: transparent;
    border-color: #fff;
}

.login-process .container form {
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 0 40px;
    height: 100%;
}

.login-process .container input,
.login-process .container select {
    background-color: #eee;
    border: none;
    margin: 8px 0;
    padding: 10px 15px;
    font-size: 15px;
    border-radius: 8px;
    width: 100%;
    outline: none;
}

.login-process .form-container {
    position: absolute;
    top: 0;
    height: 100%;
    transition: all 0.6s ease-in-out;
}

.login-process .sign-in-form {
    left: 0;
    width: 50%;
    z-index: 2;
}

.login-process .container.active .sign-in-form {
    transform: translateX(100%);
}

.login-process .sign-up-form {
    left: 0;
    width: 50%;
    opacity: 0;
    z-index: 1;
}

.login-process .container.active .sign-up-form {
    transform: translateX(100%);
    opacity: 1;
    z-index: 5;
    animation: move 0.6s;
}

.login-process .toggle-container {
    position: absolute;
    top: 0;
    left: 50%;
    width: 50%;
    height: 100%;
    overflow: hidden;
    transition: all 0.6s ease-in-out;
    border-radius: 150px 0 0 100px;
    z-index: 1000;
}

.login-process .container.active .toggle-container {
    transform: translateX(-100%);
    border-radius: 0 150px 100px 0;
}

.login-process .toggle {
    background-color: #512da8;
    height: 100%;
    background: linear-gradient(to right, #5c6bc0, #0056b3);
    color: #fff;
    position: relative;
    left: -100%;
    height: 100%;
    width: 200%;
    transform: translateX(0);
    transition: all 0.6s ease-in-out;
}

.login-process .container.active .toggle {
    transform: translateX(50%);
}

.login-process .toggle-panel {
    position: absolute;
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 0 30px;
    text-align: center;
    top: 0;
    transform: translateX(0);
    transition: all 0.6s ease-in-out;
}

.login-process .toggle-left-panel {
    transform: translateX(-200%);
}

.login-process .container.active .toggle-left-panel {
    transform: translateX(0);
}

.login-process .toggle-right-panel {
    right: 0;
    transform: translateX(0);
}

.login-process .container.active .toggle-right-panel {
    transform: translateX(200%);
}

.login-process .logo {
    margin-top: -5px;
}

.heading {
    font-size: 32px;
}

.login-process .btn-panel {
    display: flex;
    flex-direction: row;
    width: 280px;
    justify-content: space-between;
}
